import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './AboutUs.module.css';
import avr7 from '../../images/avr_7.jpeg';
import CustomButton from '../../components/CustomButton/CustomButton';
import ContactModal from '../../components/ContactModal/ContactModal';

function AboutUs() {
  const navigate = useNavigate('');

  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpenModal = () => {
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  return (
    <div id="about-us" className={styles.aboutUsBox}>
      <div className={styles.aboutUsDetails}>
        <h1 className={styles.aboutUsTitle}>Despre noi</h1>

        <p>
          AVR Weld Solutions, specializat n industria de confecii metalice,
          fabricarea i montarea traseelor de tuburi metalice destinate
          transportului lichidelor i gazelor industriale, precum i realizarea
          de mici structuri metalice. Cu o experien de peste 25 de ani n
          domeniu i deinnd autorizaie ISCIR n sudur, suntem mndri s v
          prezentm realizrile noastre remarcabile n Europa i Africa.
        </p>
        <p>
          Mndria noastr este abilitatea de a oferi soluii la cheie pentru
          proiectele industriale, concentrndu-ne pe fabricarea i instalarea
          sistemelor de conducte metalice destinate transportului <br />
          lichidelor i gazelor industriale. Flexibilitatea noastr ne permite
          s ne adaptm la cerinele specifice ale fiecrui proiect,
          asigurndu-ne c rezultatul final este ntotdeauna de cea mai nalt
          calitate.
        </p>

        <div className={styles.aboutUsBtnBox}>
          <CustomButton
            text="Gallery"
            textColor="black"
            bgColor="white"
            borderColor="black"
            fontWeight={700}
            hoverTextColor="white"
            hoverBgColor="black"
            borderSolid="1px solid black"
            borderRadius="15px"
            onClick={() => navigate('/Gallery')}
          />
          <CustomButton
            text="Contact Us"
            textColor="black"
            bgColor="white"
            borderColor="black"
            fontWeight={700}
            hoverTextColor="white"
            hoverBgColor="black"
            borderSolid="1px solid black"
            borderRadius="15px"
            onClick={handleOpenModal}
            // onClick={() => navigate('/Contact')}
          />
        </div>
        {isModalOpen && <ContactModal onClose={handleCloseModal} />}
      </div>

      <div className={styles.aboutUsImgBox}>
        <img
          className={styles.aboutUsImg}
          src={avr7}
          alt="avr_7"
          width={800}
          height={600}
        />
      </div>
    </div>
  );
}

export default AboutUs;
